import React, { useCallback, useMemo } from 'react';
import {
  Grid,
  FormGroup,
  FormControlLabel,
  Checkbox,
  TextField,
  Radio,
  Button,
  FormHelperText,
  RadioGroup,
  makeStyles,
} from '@material-ui/core';
import { useForm } from 'react-hook-form';
import Section from 'components/Forms/MentoringApply/Section';
import Fieldset from 'components/Forms/MentoringApply/Fieldset';
import { MentoringApplyFormDto } from 'models/forms';
import {
  jobs,
  positions,
  careers,
  applyReasons,
  workingTimes,
  workingLocations,
  ApplyReason,
  WorkingLocation,
} from 'src/constants/applyForm';

const useStyles = makeStyles(theme => ({
  root: {
    '& .MuiFormLabel-asterisk': {
      fontWeight: 'bold',
      color: theme.palette.primary.main,
    },
  },
  options: {
    borderRadius: theme.shape.borderRadius,
    marginRight: 7,
    paddingRight: 9,
    '&:hover': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}));

interface Props {
  onSubmit?: (data: MentoringApplyFormDto) => void;
}
const MentoringApplyForm = ({ onSubmit }: Props) => {
  const styles = useStyles();
  const { register, handleSubmit, errors, watch } = useForm();
  const formValues = watch();

  const hasPosition = useMemo(() => {
    return formValues.positions && formValues.positions.length > 0;
  }, [formValues.positions]);

  const checkedExtraReason = useMemo(() => {
    return formValues.applyReasons && formValues.applyReasons.includes('');
  }, [formValues.applyReasons]);

  const checkedExtraLocation = useMemo(() => {
    return formValues.workingLocations && formValues.workingLocations.includes('');
  }, [formValues.workingLocations]);

  const onSubmitHandler = useCallback(
    data => {
      const formData = { ...data };
      const { job, otherJob, applyReasons, otherReason, workingLocations, otherLocation } = formData;
      if (job === '') {
        formData.job = otherJob;
        delete formData.otherJob;
      }
      if (checkedExtraReason) {
        const index = applyReasons.findIndex((reason: ApplyReason) => reason === '');
        formData.applyReasons[index] = otherReason;
        delete formData.otherReason;
      }

      if (checkedExtraLocation) {
        const index = workingLocations.findIndex((location: WorkingLocation) => location === '');
        formData.workingLocations[index] = otherLocation;
        delete formData.otherLocation;
      }

      onSubmit?.(formData);
    },
    [checkedExtraLocation, checkedExtraReason, onSubmit]
  );

  return (
    <Grid
      component="form"
      container
      noValidate
      autoComplete="off"
      onSubmit={(e: any) => e.preventDefault()}
      className={styles.root}
    >
      <Section title="   .">
        <Fieldset label="" required={true} error={Boolean(errors.email)}>
          <TextField
            name="email"
            type="email"
            placeholder="lubycon@gmail.com"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: ' ',
              },
              pattern: {
                value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/,
                message: '  ',
              },
            })}
            error={Boolean(errors.email)}
            helperText={errors.email?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="" required={true} error={Boolean(errors.name)}>
          <TextField
            name="name"
            placeholder=""
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: ' ',
              },
              pattern: {
                value: /[a-zA-Z-]+/,
                message: '  ',
              },
            })}
            error={Boolean(errors.name)}
            helperText={errors.name?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="  (- )" required={true} error={Boolean(errors.phoneNumber)}>
          <TextField
            name="phoneNumber"
            type="tel"
            placeholder="01012341234"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '  ',
              },
              pattern: {
                value: /^01\d\d{4}\d{4}$/,
                message: '   ',
              },
            })}
            error={Boolean(errors.phoneNumber)}
            helperText={errors.phoneNumber?.message}
          />
        </Fieldset>
      </Section>
      <Section title="    ?">
        <Fieldset label="" required={true} error={Boolean(errors.job)}>
          <RadioGroup row defaultValue={jobs[0]}>
            {jobs.map(job => (
              <FormControlLabel
                key={job}
                name="job"
                label={job}
                value={job}
                control={<Radio />}
                inputRef={register({
                  required: {
                    value: true,
                    message: ' ',
                  },
                })}
                className={styles.options}
              />
            ))}
          </RadioGroup>
          {formValues.job === '' && (
            <TextField
              name="otherJob"
              placeholder=" "
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: ' ',
                },
              })}
              error={Boolean(errors.otherJob)}
              helperText={errors.otherJob?.message ?? ''}
            />
          )}
          {errors.job?.message && <FormHelperText>{errors.job?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset label="  (  )">
          <FormGroup row>
            {positions.map(position => (
              <FormControlLabel
                name="positions"
                key={position}
                label={position}
                value={position}
                inputRef={register()}
                control={<Checkbox />}
                className={styles.options}
              />
            ))}
          </FormGroup>
        </Fieldset>
        {hasPosition && (
          <Fieldset label="    ?" error={Boolean(errors.career)}>
            <RadioGroup row defaultValue={careers[0]}>
              {careers.map(carrer => (
                <FormControlLabel
                  key={carrer}
                  name="career"
                  label={carrer}
                  value={carrer}
                  inputRef={register({
                    required: {
                      value: true,
                      message: ' !',
                    },
                  })}
                  control={<Radio />}
                  className={styles.options}
                />
              ))}
            </RadioGroup>
            {errors.career && <FormHelperText>{errors.career?.message ?? ''}</FormHelperText>}
          </Fieldset>
        )}
      </Section>
      <Section title=" ">
        <Fieldset label="    ? (  )">
          <FormGroup>
            {applyReasons.map(reason => (
              <FormControlLabel
                name="applyReasons"
                key={reason}
                label={reason}
                value={reason}
                inputRef={register()}
                control={<Checkbox />}
                className={styles.options}
              />
            ))}
          </FormGroup>
          {checkedExtraReason && (
            <TextField name="otherReason" placeholder=" " variant="outlined" inputRef={register()} />
          )}
        </Fieldset>
        <Fieldset
          label="    1      ?"
          required={true}
          error={Boolean(errors.workingTime)}
        >
          <RadioGroup row defaultValue={workingTimes[0]}>
            {workingTimes.map(time => (
              <FormControlLabel
                key={time}
                name="workingTime"
                label={time}
                value={time}
                control={<Radio />}
                inputRef={register({
                  required: {
                    value: true,
                    message: ' ',
                  },
                })}
                className={styles.options}
              />
            ))}
          </RadioGroup>
          {errors.workingTime?.message && <FormHelperText>{errors.workingTime?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset
          label="      ? (  )"
          required={true}
          error={Boolean(errors.workingLocations)}
        >
          {workingLocations.map(location => (
            <FormControlLabel
              key={location}
              name="workingLocations"
              label={location}
              value={location}
              control={<Checkbox />}
              inputRef={register({
                required: {
                  value: true,
                  message: '  ',
                },
              })}
              className={styles.options}
            />
          ))}
          {checkedExtraLocation && (
            <TextField
              name="otherLocation"
              placeholder="  "
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: ' ',
                },
              })}
            />
          )}
          {errors.workingLocations && <FormHelperText>{errors.workingLocations?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset label="      ?  ,    ?">
          <TextField name="projectItem" multiline={true} variant="outlined" inputRef={register()} />
        </Fieldset>
        <Fieldset label="       !">
          <TextField name="expectThing" multiline={true} variant="outlined" inputRef={register()} />
        </Fieldset>
      </Section>
      <Section>
        <Fieldset
          label="  ,         
                  !"
        >
          <FormControlLabel
            name="isAgreeToCollectTalentPool"
            label=""
            inputRef={register()}
            control={<Checkbox />}
          />
        </Fieldset>
        <Button
          type="submit"
          color="primary"
          size="large"
          variant="contained"
          fullWidth={true}
          onClick={handleSubmit(onSubmitHandler)}
        >
          
        </Button>
      </Section>
    </Grid>
  );
};

export default MentoringApplyForm;
